import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IManagedObject, IOperation } from '@c8y/client';
import { gettext, ModalSelectionMode } from '@c8y/ngx-components';
import { get } from 'lodash-es';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BehaviorSubject, combineLatest, from, Observable, of } from 'rxjs';
import { distinctUntilChanged, map, shareReplay, switchMap } from 'rxjs/operators';
import { RepositoryService } from '../repository.service';
import { RepositorySelectModalComponent } from '../select-modal/repository-select-modal.component';
import {
  DeviceSoftware,
  DeviceSoftwareChange,
  RepositoryType,
  SoftwareFilterCriteria
} from './../repository.model';

@Component({
  selector: 'c8y-installed-software',
  templateUrl: 'installed-software.component.html'
})
export class InstalledSoftwareComponent implements OnInit {
  @Input() device: IManagedObject;
  @Input() softwareList: DeviceSoftware[];
  @Input() deviceSoftwareChanges: DeviceSoftwareChange[];
  @Input() deviceSoftwareChangesOperation: IOperation;
  @Input() deviceSoftwareChangesInProgress: boolean;
  @Input() deviceTypeQuery: object;
  @Output() changes = new EventEmitter<DeviceSoftwareChange[]>();
  @Output() showSoftwareChanges = new EventEmitter<void>();

  showFilter: boolean = false;
  supportsSoftwareOperations: boolean = false;
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  softwareTypeFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  filterCriteria$: Observable<SoftwareFilterCriteria>;

  private readonly operationTypes = ['c8y_SoftwareUpdate', 'c8y_SoftwareList', 'c8y_Software'];

  constructor(private repository: RepositoryService, private bsModal: BsModalService) {
    this.filterCriteria$ = combineLatest(this.textFilter$, this.softwareTypeFilter$).pipe(
      map(([textFilter, softwareTypeFilter]) => ({
        name: textFilter,
        softwareType: softwareTypeFilter
      })),
      map(filterCriteria =>
        !filterCriteria.name && !filterCriteria.softwareType ? null : filterCriteria
      )
    );
  }

  ngOnInit(): void {
    const supportedOperations = get(this.device, 'c8y_SupportedOperations', []);
    this.supportsSoftwareOperations = this.operationTypes.some(
      operationType => supportedOperations.indexOf(operationType) > -1
    );
  }

  installSoftware() {
    this.displaySoftwareSelectModal({
      title: gettext('Install software'),
      labels: { ok: gettext('Install') },
      repositoryEntriesWithVersions$: of([]),
      repositoryEntriesWithVersionsFn$: modal =>
        this.getInstallableSoftwareListWithVersions$(modal.content.searchTerm)
    }).subscribe(softwareToInstall => {
      this.emitSoftwareInstall(softwareToInstall);
      this.showSoftwareChanges.emit();
    });
  }

  updateSoftware(softwareToRemove) {
    this.displaySoftwareSelectModal({
      title: gettext('Update software'),
      labels: { ok: gettext('Update') },
      showFilter: false,
      repositoryEntriesWithVersions$: this.getSingleSoftwareWithVersions$(softwareToRemove)
    }).subscribe(softwareToInstall => {
      this.emitSoftwareInstall(softwareToInstall);
      this.showSoftwareChanges.emit();
    });
  }

  removeSoftware(softwareToRemove) {
    this.emitSoftwareRemoval([softwareToRemove]);
  }

  getInstallableSoftwareListWithVersions$(searchTerm$: BehaviorSubject<string>) {
    const installedSoftwareNames = (this.softwareList || []).map(s => s.name);
    return searchTerm$.pipe(
      distinctUntilChanged(),
      switchMap(searchTerm =>
        this.repository.listRepositoryEntries(RepositoryType.SOFTWARE, {
          query: this.deviceTypeQuery,
          partialName: searchTerm,
          params: { pageSize: 100 }
        })
      ),
      map(({ data }) => data),
      map(softwareList => {
        return softwareList.filter(software => {
          return !installedSoftwareNames.includes(software.name);
        });
      }),
      map(softwareList => this.attachVersions(softwareList)),
      shareReplay(1)
    );
  }

  getSingleSoftwareWithVersions$(software: DeviceSoftware) {
    return from(
      this.repository.listRepositoryEntries(RepositoryType.SOFTWARE, {
        query: { name: software.name }
      })
    ).pipe(
      map(({ data }) => data),
      map(softwareList => this.attachVersions(softwareList)),
      shareReplay(1)
    );
  }

  attachVersions(softwareList: IManagedObject[]) {
    softwareList.forEach(software => {
      software.versions = this.repository.listBaseVersions(software);
    });
    return softwareList;
  }

  displaySoftwareSelectModal(initialStateOverrides) {
    const initialState = {
      repositoryType: RepositoryType.SOFTWARE,
      subTitle: gettext('Available softwares matching the device type'),
      mode: ModalSelectionMode.MULTI,
      icon: 'c8y-tools',
      disableSelected: false,
      selected: this.softwareList,
      ...initialStateOverrides
    };
    const modal = this.bsModal.show(RepositorySelectModalComponent, {
      ignoreBackdropClick: true,
      class: 'modal-sm',
      initialState
    });

    if (initialStateOverrides.repositoryEntriesWithVersionsFn$) {
      modal.content.repositoryEntriesWithVersions$ =
        initialStateOverrides.repositoryEntriesWithVersionsFn$(modal);
    }

    modal.content.load.next();
    return modal.content.resultEmitter;
  }

  emitSoftwareInstall(items: DeviceSoftware[]) {
    this.changes.emit(
      items.map(item => {
        return { ...item, action: 'install' };
      })
    );
  }

  emitSoftwareRemoval(items: DeviceSoftware[]) {
    this.changes.emit(
      items.map(item => {
        return { ...item, action: 'delete' };
      })
    );
  }
}
