import { AfterContentInit, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IManagedObject, IResultList, Paging } from '@c8y/client';
import { filter, get, set } from 'lodash-es';
import { BehaviorSubject, combineLatest, Observable, of } from 'rxjs';
import { debounceTime, distinctUntilChanged, map, switchMap, tap } from 'rxjs/operators';
import { DeviceSoftware, DeviceSoftwareChange, SoftwareFilterCriteria } from '../repository.model';
import { AdvancedSoftwareService } from './advanced-software.service';

@Component({
  selector: 'c8y-device-software-list',
  templateUrl: 'device-software-list.component.html'
})
export class DeviceSoftwareListComponent implements OnInit, AfterContentInit {
  @Input() set softwareList(softwareList: DeviceSoftware[]) {
    if (softwareList !== null) {
      this.legacySoftwareList$.next(softwareList);
    }
  }
  @Input() device: IManagedObject;
  @Input() deviceSoftwareChanges: DeviceSoftwareChange[];
  @Input() filterCriteria$: Observable<SoftwareFilterCriteria> = of(null);
  @Output() update = new EventEmitter<DeviceSoftware>();
  @Output() remove = new EventEmitter<DeviceSoftware>();
  @Output() onListEmpty: EventEmitter<boolean> = new EventEmitter();
  softwareItems$: Observable<IResultList<DeviceSoftware>>;
  showUpdate: boolean;
  showRemove: boolean;
  emptyList: boolean;
  noSearchResults: boolean;
  supportsSoftwareOperations: boolean = false;

  private readonly operationTypes = ['c8y_SoftwareUpdate', 'c8y_SoftwareList', 'c8y_Software'];

  private legacySoftwareList$: BehaviorSubject<DeviceSoftware[]> = new BehaviorSubject([]);

  constructor(private advancedSoftwareService: AdvancedSoftwareService) {}

  ngOnInit(): void {
    this.softwareItems$ = combineLatest(this.filterCriteria$, this.legacySoftwareList$).pipe(
      debounceTime(300),
      distinctUntilChanged(),
      switchMap(([filterCriteria, legacySoftwareList]) => {
        if (legacySoftwareList) {
          return of(this.getLegacySoftwareList(filterCriteria)).pipe(
            map(resultList => ({ resultList, filterCriteria }))
          );
        } else {
          return this.getAdvancedSoftwareList(filterCriteria).then(resultList => ({
            resultList,
            filterCriteria
          }));
        }
      }),
      tap(({ resultList, filterCriteria }) => {
        this.notifyListEmpty(!resultList?.paging?.totalPages && !filterCriteria);
        this.noSearchResults = !resultList?.paging?.totalPages && !!filterCriteria;
      }),
      map(({ resultList }) => resultList)
    );
    const supportedOperations = get(this.device, 'c8y_SupportedOperations', []);
    this.supportsSoftwareOperations = this.operationTypes.some(
      operationType => supportedOperations.indexOf(operationType) > -1
    );
  }

  ngAfterContentInit() {
    this.showUpdate = this.update.observers.length > 0;
    this.showRemove = this.remove.observers.length > 0;
  }

  isSoftwareGoingToBeChanged(software: DeviceSoftware): boolean {
    const relevantChanges = filter(this.deviceSoftwareChanges, software);
    return relevantChanges.length > 0;
  }

  private getAdvancedSoftwareList(
    filterCriteria: SoftwareFilterCriteria
  ): Promise<IResultList<DeviceSoftware>> {
    const queryFilter = { deviceId: this.device.id };
    if (filterCriteria?.name) {
      set(queryFilter, 'name', `*${filterCriteria.name}*`);
    }
    if (filterCriteria?.softwareType) {
      set(queryFilter, 'type', `${filterCriteria.softwareType}`);
    }
    return this.advancedSoftwareService.list(queryFilter) as unknown as Promise<
      IResultList<DeviceSoftware>
    >;
  }

  private getLegacySoftwareList(
    filterCriteria: SoftwareFilterCriteria
  ): IResultList<DeviceSoftware> {
    const data = filterCriteria
      ? this.legacySoftwareList$.value.filter(item => {
          let match = true;
          if (filterCriteria?.name) {
            match = match && item.name?.includes(filterCriteria.name);
          }
          if (filterCriteria?.softwareType) {
            match = match && item.softwareType === filterCriteria.softwareType;
          }

          return match;
        })
      : this.legacySoftwareList$.value;
    return {
      data,
      res: null,
      paging: {
        totalPages: data.length
      } as Paging<DeviceSoftware>
    };
  }

  private notifyListEmpty(isEmpty: boolean): void {
    this.emptyList = isEmpty;
    this.onListEmpty.emit(isEmpty);
  }
}
