import { Injectable } from '@angular/core';
import { ApplicationService, FetchClient, IManagedObject, IResultList, Service } from '@c8y/client';

@Injectable()
export class AdvancedSoftwareService extends Service<IManagedObject> {
  private static readonly applicationName = 'advanced-software-mgmt';

  protected baseUrl: string = 'service/advanced-software';
  protected listUrl: string = 'software';
  protected propertyName = 'softwareList';

  private isAvailable: Promise<boolean>;

  constructor(client: FetchClient, private applicationService: ApplicationService) {
    super(client);
  }

  list(filter: object = {}): Promise<IResultList<IManagedObject>> {
    return super.list(filter);
  }

  isASMAvailable(): Promise<boolean> {
    if (this.isAvailable === undefined) {
      this.isAvailable = this.applicationService
        .isAvailable(AdvancedSoftwareService.applicationName)
        .then(result => !!result?.data);
    }

    return this.isAvailable;
  }
}
